import React, { Component } from 'react';
import './Experience.css';
import ExpContent from './ExpContent'

let experiences = require('../resources/experience.json')

export default class Experience extends Component {
  constructor(props){
    super(props);
    experiences=experiences.sort(this.compare)
  }
  compare(a, b) {
    if (a.startDate < b.startDate ) return 1;
    if (b.startDate  < a.startDate ) return -1;
    return 0;
  }
  render() {
    const d={
      width:'200px',
      height:'200px'
    }
    const exp = experiences.map((exp, index) => {
      return this.props.checkFilter(exp.tags)? <ExpContent key={index}  data={exp} dimensions={d}/>: null
    });
    return (
      <div className="experience">
      <h1 className="section-title" style={{backgroundColor:'black'}}>Experience</h1>
        {exp}
      </div>
    );
  }
}
